import React from 'react';
import { Route, HashRouter, useLocation, Switch } from 'react-router-dom';
import Header from './components/Header';
import Footer from './components/Footer';
import HeaderCopy from './components/HeaderCopy';
import Home from './pages/Home';
import Login from './pages/Login';
import SignUp from './pages/SignUp';
import theme from './Utils/Theme';
import { ThemeProvider } from '@material-ui/core/styles';
import Preview from './pages/Preview';
import Invite from './pages/Invite';
import Integrates from './pages/Integrates';
import MyProfile from './pages/MyProfile';
import Pricing from './pages/Pricing';
import ShopifyTools from './pages/ShopifyTools';
import Payment from './pages/Payment';
import FacebookTools from './pages/Facebooktools';
import WhatsappTools from './pages/WhatsappTools';

function FooterWithConditionalRendering() {
  const location = useLocation();
  const footernotAllowed = location.pathname === '/login' || location.pathname === '/signup' || location.pathname === '/my-profile' || location.pathname === '/integrates/shopify';

  return !footernotAllowed ? <Footer /> : null;
}

function App() {
  return (
    <>
    <ThemeProvider theme={theme}>
      <HashRouter>
        {/* <Header /> */}
        <HeaderCopy/>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/signup" component={SignUp} />
            <Route exact path="/preview" component={Preview} />
            <Route exact path="/invite" component={Invite} />
            <Route exact path="/integrates" component={Integrates} />
            <Route exact path="/my-profile" component={MyProfile} />
            <Route exact path="/pricing" component={Pricing} />
            <Route exact path="/integrates/shopify" component={ShopifyTools} />
            <Route exact path="/pricing/payment" component={Payment} />
            <Route exact path="/integrates/facebook" component={FacebookTools} />
            <Route exact path="/integrates/whatsapp" component={WhatsappTools} />
          </Switch>
          <FooterWithConditionalRendering/>
      </HashRouter>
      </ThemeProvider>
    </>
  );
}

export default App;